import React, { useRef } from 'react'
import "../Styles/Contact.css";
import {Heading, FormControl, FormLabel, FormErrorMessage,FormHelperText,Box,Input,Textarea,Button} from "@chakra-ui/react";
import emailjs from "@emailjs/browser"

export default function Contact() {
let form =useRef();

const sendEmail = (e) => {
  e.preventDefault();
  console.log(e);

  emailjs.sendForm('service_uwtxc7d', 'template_82dncwi', form.current, '9MDZYzpuS3jGtBi9P')
    .then((result) => {
        console.log(result.text);
    }, (error) => {
        console.log(error.text);
    });
};

  return (
    <div id='ContactMain'>
        <Box>
            <Heading>Contact</Heading>
            <span id='emptySpan'>*****</span>
            <p>Feel free to Contact me by submitting the form below and I will get back to you as soon as possible</p>
        </Box>
      
      <Box>
        <form onSubmit={sendEmail}  ref={form}>
           <FormControl  >
               <FormLabel>Name</FormLabel>
               <Input style={{outline:"none"}} name="to_name"  type={"text"} placeholder='Enter Your Name' required />
               <FormLabel>Email</FormLabel>
               <Input outlineColor={'transparent'} name="from_name" type={"email"} placeholder='Enter Your Email' required />
               <FormLabel>Message</FormLabel>
               <Textarea rows={10} name="message" placeholder='Enter Your Message' />
              <Input  style={{backgroundColor:"#facf0f", padding:"1% 1%"}} id='submitForm' type={'submit'} value="SUBMIT" />
           </FormControl>
           </form>
      </Box>
      

    </div>
  )
}
